import React from "react";

export const AvatarUser = ({ firstLetter, number, wh, wt }) => {
  console.log(firstLetter, number);
  let background = "";

  switch (number) {
    case 1:
      background = "green-400";
      break;
    case 2:
      background = "gray-400";
      break;
    case 3:
      background = "red-400";
      break;
    case 4:
      background = "blue-400";
      break;
    case 5:
      background = "pink-400";
      break;

    default:
      background = "yellow-400";
      break;
  }
  return (
    <div
      className={`w-${wh} h-${wh}  rounded-full flex justify-center items-center bg-${background}`}
    >
      <span className={`text-${wt}xl uppercase`}>{firstLetter}</span>
    </div>
  );
};
